
import { defineComponent } from 'vue'

export default defineComponent({
  name: 'EpicInput',
  props: {
    modelValue: {
      type: String,
      required: true,
    },
    label: {
      type: String,
      default: '',
    },
  },
  methods: {
    updateValue(value: string) {
      this.$emit('update:modelValue', value)
    },
  },
})
